import List from './components/List'
import About from './components/About'

export const routes = [
  {
    path: '*',
    redirect: '/'
  },
  {
    path: '/',
    component: List
  },
  {
    path: '/about',
    name: 'about',
    component: About,
    meta: {
      requiresAuth: true
    }
  }
];


