<template>
  <div>
    <header class="p-6 w-full bg-gray-900 text-white flex items-end leading-none">
      <h1>{{ pageTitle }}</h1>
    </header>

    <div v-if="!tableActive" class="m-4 p-2 md:w-1/2 rounded bg-gray-100">
      <section class="m-4 hidden">
        <label class="mx-4 text-xs font-bold uppercase">Kies een spel</label>
        <div class="relative inline-block w-full rounded-md bg-green-500 cursor-pointer">
          <select v-model="selectors.gameType.selected" class="w-full pl-4 py-2 pr-8 bg-green-500 text-white rounded-md appearance-none cursor-pointer">
            <option
              v-for="(car, index) in selectors.gameType.items"
              :key="index"
              :value="{ id: car.id, name: car.name }"
            >{{ car.name }}</option>
          </select>
          <span class="absolute top-0 right-0 mt-2 mr-4 text-white cursor-pointer">|</span>
        </div>
        <div class="text-sm opacity-50 hidden">
          <div>selectors.gameType.selected.id: {{ selectors.gameType.selected.id }}</div>
          <div>selectors.gameType.selected.name: {{ selectors.gameType.selected.name }}</div>
        </div>
      </section>

      <section class="m-4">
        <label class="mx-4 text-xs font-bold uppercase">Maak spelers aan (min. 2)</label>
        <div class="flex flex-col flex-wrap -mx-1">
          <div v-for="(player, index) in game.players" :key="index" class="w-full px-1 pb-2">
            <input
              type="text"
              :placeholder="'Naam speler ' + (index + 1)"
              class="w-full px-4 py-2 bg-green-500 text-white rounded-md"
              v-model="game.players[index].name"
            >
          </div>
          <div class="px-1">
            <button
              @click.prevent="removePlayer"
              class="px-12 py-2 bg-black rounded-md text-white font-bold text-md outline-none"
              :class="[game.playerCount === 2 ? 'opacity-25 cursor-not-allowed pointer-events-none' : '']"
            >-</button>
            <button
              @click.prevent="addPlayer"
              class="ml-1 px-12 py-2 bg-black rounded-md text-white font-bold text-md outline-none"
            >+</button>
          </div>
        </div>
        <div class="text-sm opacity-50 hidden">
          {{ game.playerCount }}
          <div>game.players: <div v-for="(player, index) in game.players" :key="index">{{ player }}</div></div>
        </div>
      </section>

      <section class="flex justify-end m-4 mt-8">
        <button @click="createScoreboard" class="px-6 py-3 bg-blue-900 text-white rounded-full">Maak Scorebord aan</button>
      </section>
    </div>











    <!-- TABLE with RESULTS -->

    <div :class="[tableActive ? '' : 'hidden']">
      <section class="m-4">
        <table class="w-full bg-gray-100">
          <thead class="bg-gray-800 text-white">
            <tr>
              <td class="p-4 w-32">#</td>
              <td
                v-for="(player, index) in game.players"
                :key="index"
                class="p-4 px-8"
              >
                <div v-if="game.players[index].name">
                  {{ game.players[index].name }} <sup v-if="game.players[index].name === game.currentWinner" class="bg-blue-700 px-2 py-1 rounded-full">Leader</sup>
                </div>
                <div v-else class="opacity-50 italic">Speler {{ index+1 }}</div>
              </td>
            </tr>
          </thead>
          <tbody>

            <tr v-if="game.round < 1">
              <td class="p-4">Score</td>
              <td v-for="(player, index) in game.players" :key="index" class="p-4 px-8">
                <span class="text-xl">0</span>
              </td>
            </tr>

            <tr v-for="(round, r_index) in game.round" :key="r_index" :class="[r_index % 2 === 0 ? 'bg-gray-200' : 'bg-gray-100']">
              <td class="p-4 w-32 text-sm">Ronde {{ round }}</td>
              <td v-for="(player, index) in game.players" :key="index" class="p-4 px-8">
                <div v-if="r_index > 0" class="relative">
                  <span v-if="game.players[index].scores[round-1].score - game.players[index].scores[round-2].score >= 0" class="tag bg-green-600">+{{ game.players[index].scores[round-1].score - game.players[index].scores[round-2].score }}</span>
                  <span v-else class="tag bg-red-600">{{ game.players[index].scores[round-1].score - game.players[index].scores[round-2].score }}</span>
                  <span class="text-xl">{{ game.players[index].scores[round-1].score }}</span>
                </div>
                <div v-else>
                  <span class="text-xl">{{ game.players[index].scores[round-1].score }}</span>
                </div>
              </td>
            </tr>

            <tr class="bg-transparent text-white">
              <td class="p-4 w-32 text-sm"></td>
              <td v-for="(player, index) in game.players" :key="index" class="px-4 py-2">
                <div class="flex">
                  <input type="number" step="2" :placeholder="'score van ' + player.name" class="outline-none w-full px-4 py-2 bg-green-500 text-white rounded-md" v-model="game.players[index].newInputScore">
                </div> 
              </td>
            </tr>

          </tbody>
        </table>
        <div class="text-sm opacity-50 hidden">
          <div>game.round: {{ game.round }}</div>
          <div>game.playerCount: {{ game.playerCount }}</div>
          <div>game.currentWinner: {{ game.currentWinner }}</div>
          <div>game.players: <div v-for="(player, index) in game.players" :key="index">{{ player }}</div></div>
        </div>
      </section>

      <section class="flex items-center justify-between m-4">
        <button @click.prevent="nextRound" class="px-6 py-3 bg-blue-900 text-white rounded-full">Volgende Ronde</button>
        <button @click.prevent="reset" class="px-4 py-2 bg-red-900 text-xs text-white rounded-full">Reset</button>
      </section>
    </div>

  </div>
</template>

<script>
export default {
  name: 'ScoreBoard',
  data() {
    return {
      pageTitle: 'Scoreboard',

      tableActive: false,

      selectors: {
        gameType: {
          selected: {id: 0, name: 'Gregoriaans Pokeren'},
          items: [
            {id: 0, name: 'Gregoriaans Pokeren'},
            {id: 1, name: 'Hartenjagen'},
            {id: 2, name: 'Wiezen'},
            {id: 3, name: 'Kingen'}
          ]
        }
      },

      game: {
        playerCount: 2,
        round: 0,
        currentWinner: '',
        players: [
          {
            name: '',
            newInputScore: null,
            lastScore: 0,
            totalScore: 0,
            scores: []
          },
          {
            name: '',
            newInputScore: null,
            lastScore: 0,
            totalScore: 0,
            scores: []
          }
        ]
      }
    }
  },
  methods: {
    addPlayer() {
      this.game.playerCount++;
      this.game.players.push({
        name: '',
        newInputScore: null,
        lastScore: 0,
        totalScore: 0,
        scores: []
      });
    },
    removePlayer(index) {
      if ( this.game.playerCount > 2 ) {
        this.game.playerCount--;
        this.game.players.splice(index, 1);
      }
    },
    nextRound() {
      var nextRound = ++this.game.round;

      for (var i = 0; i < this.game.players.length; i++) {

        // If input is empty make value 0
        if (this.game.players[i].newInputScore === null) {
          var inputScore = 0;
        }
        else {
          var inputScore = parseInt(this.game.players[i].newInputScore);
        }

        // Get most recent score of player
        var currentScore = parseInt(this.game.players[i].lastScore);

        // Calculate the new score
        var newScore = currentScore + inputScore;

        // Save the latest score
        this.game.players[i].lastScore = newScore;

        // Calculate totat score per player
        this.game.players[i].totalScore = this.game.players[i].totalScore + inputScore;

        // Push the new values
        this.game.players[i].scores.push({
          round: nextRound,
          score: newScore
        });

        // Empty this player's score input value for next round
        this.game.players[i].newInputScore = null;
      }

      var highScore = 0;

      for (var i = 0; i < this.game.players.length; i++) {
        if ( this.game.players[i].lastScore === highScore ) {
          this.game.currentWinner = '';
        }
        if ( this.game.players[i].lastScore > highScore ) {
          highScore = this.game.players[i].lastScore;
          this.game.currentWinner = this.game.players[i].name;
        }
      }
    },
    sum(a, b) {
      return parseInt(a + b);
    },
    sub(a, b) {
      return parseInt(a - b);
    },
    createScoreboard() {
      this.tableActive = !this.tableActive;
    },
    reset() {
      this.game.playerCount = 2;
      this.game.round = 0;
      this.game.currentWinner = '';
      this.game.players = [];
      this.game.players.push(
        {
          name: '',
          newInputScore: null,
          lastScore: 0,
          totalScore: 0,
          scores: []
        }
      );
      this.game.players.push(
        {
          name: '',
          newInputScore: null,
          lastScore: 0,
          totalScore: 0,
          scores: []
        }
      );
      this.tableActive = false;
    }
  }
}
</script>

<style scoped>
  select {
    text-transform: capitalize;
  }
  ::placeholder {
    color: rgba(255,255,255,.75);
    font-style: italic;
  }
  .tag {
    position: absolute;
    font-size: 0.6rem;
    top: -25px;
    left: 10px;
    padding: 2px 5px;
    border-radius: .5rem;
    color: #FFF;
    opacity: .5;
  }
</style>


